<template>
	<div class="payment">

		<mineHeader></mineHeader>

		<div class="paymentcenter">

			<indentStatus>
		  		<span slot="indentName">充值余额</span>
		  	</indentStatus>

			<div class="payway-area">
				<div class="paywaytop">

					<dl>
						<dt>
							<span>充值账户：</span>
							<i v-text="mobile"></i>
						</dt>
						<dd>
							<span>充值金额：</span>
							<i>
								<input 
									:class="[
										inputSign ? 'inputActive' : ''
									]"
									v-onlynumber 
									v-model="recharge"
									@focus="inputFocus($event)"
									@blur="inputBlur($event)"
								/>
								<!-- <moneyInput className="recharge" :length="8" @change="changeinput" /> -->
							</i>


						</dd>
						<dd class="alert">输入金额需大于零</dd>
					</dl>

					<div class="indentinfo"><h4>选择支付方式</h4></div>
					<ul class="payway">
						<li v-for="(item,index) in payWays" key="index">
							<input 
								type="radio" 
								:id="item.name"  
								name="payment" 
								v-on:click="choicePayway(item.name)" 
							/>
							<label 
								:for="item.name" 
								:class="[
									payWay==item.name?'redlabel':''
								]"
							>
								<img :src="item.img" />
							</label>
						</li>
					</ul>

				</div>

				<div class="totalprice">
					应付金额：<span class="totalmoney">￥{{recharge == ''?'0.00':recharge}}</span>
				</div>

				<div class="paybtnarea">
					<input 
						type="button" 
						class="pay-submit"
						value="立即充值" 
						@click="paySubmit"/>
				</div>

				

			</div>
		</div>
		<index-footer></index-footer>



		<el-dialog 
			v-model="dialogVisible" 
			size="tiny" 
			:close-on-click-modal='false' 
			:close-on-press-escape='false' 
			:show-close="false"
		>
			<div class="img-wrap">
				<img src="data:image/png;base64," />
			</div>
			<div class="checkbtnarea">
				<!-- <p>请尽快支付完成，否则订单会被取消！</p> -->
				<p class="checkbtnarea-p">如有疑问，请联系客服0571-28270936</p>
				<router-link to="/mine/myBalance">充值完成</router-link>
				<a href="javascript:;" v-on:click="dialogVisible = false" class="btn-repay">重新充值</a>
			</div>
		</el-dialog>

	</div>
</template>

<script>

	import mineHeader from '@/components/common/mine_header'
	import IndexFooter from '@/components/common/index_footer'
	import indentStatus from '@/components/common/indentStatus'
	// import moneyInput from '@/components/common/moneyInput'

	let that;

	export default {
		name: 'payment',
		data () {
			return {

				payWays:[
					{
						name:'alipay',
						img:'/static/images/payment-alipay.png'
					},
					{
						name:'weixin',
						img:'/static/images/payment-weixin.png'
					},
					{
						name:'allinpay',
						img:'/static/images/payment-allinpay.png'
					}
				],

				payWay:'', // 支付方式

				mobile: this.$cookie.getCookie('8e3c824e1d6254b74a013799c1565538') || '',
				userId: this.$cookie.getCookie('a34ea2d96d4239d4d86803bb3cd99788') || '',

				inputSign:false,		// 输入框聚焦标志
				rechargeAmount:'',		// 输入框金额
				rechargeArr:[],			// 输入框 金额 数组 将拼接成金额
				length:8,				// 输入框数字长度限制

				account:this.$route.query.account || '', 		// 余额 微信支付 总额
				pay_code:'',   									// 支付方式 支付宝 通联
				dialogVisible:false,							// 支付 成功 失败返回后的弹窗

			}
		},
		components:{
			mineHeader,
			IndexFooter,
			indentStatus,
			// moneyInput
		},
		beforeCreate(){
			let proof  = this.$cookie.getCookie('b61a9d162ac1dfaa22d0f05bdc7f6299');

	        if(proof == 1 ){

	        }else if(proof == -1){            // 审核中

	        	this.$anchu_message.warn('认证状态中，暂无权限访问该页面');
	        	this.$router.push('/');

	        }else if(proof == -2 || proof == -3 ){     // 审核没通过 未查询到数据

	        	this.$anchu_message.error('认证失败，请重新进行审核');
	            this.$router.push('/proof');

	        }else if(proof == -4 ){     // 未提交认证

	        	this.$anchu_message.warn('未提交认证，请先认证');
	            this.$router.push('/proof');

	        }else{
	        	this.$router.push('/login');
	        }

	        that = this;
		},
		mounted(){
	        if(this.account){
	        	this.rechargeAmount = this.account;
	        }

		},
		computed:{
			recharge(){
				// 截取掉首位的 0
				if(this.rechargeArr.length >=2 && this.rechargeArr[0] == 0 && this.rechargeArr[1] != '.'){
					this.rechargeArr.shift();
				}

				this.inputValue = this.rechargeArr.join('');
				this.rechargeAmount = this.inputValue;
				return this.inputValue;
			}
		},
		// 自定义指令
		directives:{
			onlynumber: {
	            bind: function(el) {
	                el.handler = function() {
		                
		                // 金额 数组长度
		                const rechargeArrLen = that.rechargeArr.length;
		                // 输入框 最后一位字符
		                const dataStr = el.value.charAt(el.value.length-1);

		                console.log(dataStr)

		                // 删除
		                if(el.value.length < rechargeArrLen){
		                	that.rechargeArr.pop();
		                	return false;
		                }

		               	// 只能输入 数字 .
		                if(!((dataStr >=0) || (dataStr=='.'))){
		                	// 非法字符 截取掉
		                	el.value = el.value.slice(0,-1);
		                	return false;
		                }

		                // ++++++++++++++++++++++ 字符验证开始 ++++++++++++++++++++++ 
						// 首位不为 .
						if(rechargeArrLen == 0 && dataStr == '.'){
							// 非法字符 截取掉
							el.value = el.value.slice(0,-1);
							return false;
						}
						// 限制输入过多的 0 
						if(rechargeArrLen == 1 && that.rechargeArr == 0 && dataStr == '0'){
							// 非法字符 截取掉
							el.value = el.value.slice(0,-1);
							return false;
						}
						// 存在 . 不能再次输入
						if(that.spot() > 0 && dataStr == '.'){
							// 非法字符 截取掉
							el.value = el.value.slice(0,-1);
							return false;
						}
						// 小数点后只能输入两位
						if(that.spot() > 0 && rechargeArrLen > (Number(that.spot())+2) ){
							// 非法字符 截取掉
							el.value = el.value.slice(0,-1);
							return false;
						}
						// 位数满足 数字 无小数点
						if(rechargeArrLen == that.length && dataStr != '.' && that.spot() == -1){
							el.value = el.value.slice(0,that.length);
							return false;
						}
						// ++++++++++++++++++++++ 字符验证结束 ++++++++++++++++++++++

						// 合法金额字符 推入数组
		                that.rechargeArr.push(dataStr);
	                }
	                
	                el.addEventListener('input', el.handler)
	            },
	            unbind: function(el) {
	                el.removeEventListener('input', el.handler)
	            }
	        }
		},
		methods:{
			spot(){
				let tem;
				for(let i in this.rechargeArr){
					if(this.rechargeArr[i] == '.'){
						tem = i;
					}
				}

				return tem || -1;
			},
			inputFocus(e){
				this.inputSign = true;
			},
			inputBlur(e){
				this.inputSign = false;
				if(this.rechargeArr[this.rechargeArr.length-1] == '.'){
					this.rechargeArr.push('0');
					this.rechargeArr.push('0');
				}
			},

			// 选择支付方式
			choicePayway(e){
				this.payWay = e;

				if(e == 'balance'){
					this.balanceSign = true;
					// 获取账户余额信息
					this.getBalanceInfo();
					return false;
				}

				this.balanceSign = false;
			},

			// 去支付
			pay(){
				const _this = this;
				$.ajax({
					url: this.$api.payment.getCode,
					dataType: "json",
					type: 'post',
					data:{
						account:this.rechargeAmount,
						pay_code:this.payWay
					},
					success: function (res) {
						console.log(res)
					},
					error:function (err) {
						_this.$anchu_message.error('网络繁忙');
					}
				});
			},

			// 支付宝 银联支付
			postWindow(url,name1,name2,name3,value1,value2,value3){

				//创建表单对象
				let _form = $("<form></form>",{
					'id':'tempForm',
					'method':'post',
					'action':url,
					'target':'_blank',
					'style':'display:none'
				}).appendTo($('body'));

				//将隐藏域加入表单
				_form.append($("<input>",{'type':'hidden','name':name1,'value':value1}));
				_form.append($("<input>",{'type':'hidden','name':name2,'value':value2}));
				_form.append($("<input>",{'type':'hidden','name':name3,'value':value3}));

				//触发提交事件
				_form.trigger('submit');

				//表单删除
				_form.remove();
			},

		// 立即支付 按钮
		paySubmit(){

			if(this.recharge == ''){
				this.$anchu_message.warn('请输入充值金额');
				return false
			}

			if(this.rechargeAmount == 0){
				this.$anchu_message.warn('充值金额应大于零');
				return false
			}

			if(this.payWay == ''){
				this.$anchu_message.warn('请选择支付方式');
				return false
			}

			const _this = this;
			$.ajax({
				url: this.$api.payment.checkUserBack,
				dataType: "json",
				type: 'get',
				async:false,
				success: function (res) {

					// 账号被拉黑
					if(res.error == 1){
						_this.$anchu_message.error(res.error_msg);
						return false;
					}else{

						const url = _this.$api.payment.getCode;

						if (_this.payWay == 'alipay') {

							_this.postWindow(
								url,
								'account',
								'pay_code',
								'user_id',
								_this.rechargeAmount,
								'alipay',
								_this.userId
							);

							_this.dialogVisible = true;

						}else if (_this.payWay == 'weixin'){

							_this.$router.push({
						    	path:'/paywayWeixin',
						    	query:{
						    		'account': _this.rechargeAmount
						    	}
							});

							_this.dialogVisible = true;
							
						}else if(_this.payWay == 'allinpay'){

			                _this.postWindow(
			                	url,
			                	'account',
			                	'pay_code',
			                	'user_id',
			                	_this.rechargeAmount,
			                	'allinpay',
			                	_this.userId
			                );

			                _this.dialogVisible = true;
			        	}
					}
				},
				error:function (err) {
					_this.$anchu_message.error('网络繁忙');
				}
			});


        	
        	
        }
    }
}
</script>

<style>
	.payment .el-dialog__body{
		padding: 30px 0;
	}
 	.payment .el-dialog--tiny{
 		width:450px;
 		border-radius: 5px;
 		-moz-border-radius: 5px;
		-webkit-border-radius: 5px;
 	}
	.payment .el-dialog__header{
	  padding: 0;
	}

	.payment .img-wrap{
		text-align: center;
	}
	.img-wrap img{
		display: inline-block;
	}

</style>

<style lang="scss" scoped>
	

	.fade-enter-active {
	  transition: opacity .5s
	}
	.fade-leave-active{
		transition: opacity .2s
	}
	.fade-enter,.fade-leave-to{
	  opacity: 0;
	}
	.code-wrap{
		overflow: hidden;
		span{
			float: left;
			overflow: hidden;
			border: 1px solid #009e4d;
			border-right: 0;
			em{
				float: left;
				width: 36px;
				height: 36px;
				border-right: 1px solid #009e4d;
				input{
					display: inline-block;
					width: 100%;
					height: 100%;
					line-height: 36px;
					text-align: center;
					font-weight: 600;
					font-size: 16px;
				}
			}
		}
		i{
			float: left;
			line-height: 36px;
			margin-left: 12px;
			padding: 0 12px;
			background: #ebebeb;
			cursor: pointer;
			font-size: 14px;
		}

	}
	.balance-wrap{
		padding: 17px 15px;
		background: #fffcfb;
		border: 1px solid #e64346;
		margin-top: 15px;
		line-height: 24px;
		overflow: hidden;
	}
	.balance-left{
		color: #999;
		float: left;
		i{
			color: #333;
			&:after{
				content:'元';
			}
		}
	}
	.balance-center{
		float: left;
		margin-left: 15px;
		background: #f2f2f2;
		padding: 0 15px;
		border-radius: 14px;
		font-size: 14px;
		a{
			color: #009e4d;
			text-decoration: underline;

		}
	}
	.balance-rigth{
		float: right;
		color: #999;
		i{
			color: #e64346;
			&:after{
				color: #999;
				content:'元';
			}
		}
	}
	.payment{
		width: 100%;
		min-width: 1210px;
		background: #f5f5f5;
	}
	.payment .paymentcenter{
		width: 1200px;
		margin: 0 auto;
	}
	.payment .title{
		margin-bottom: 13px;
	}
	.payment .ordermsg{
		display: flex;
		justify-content: space-between;
		margin-bottom: 13px;
	}
	.payment .payway-area{
		background:#fff;
		width: 100%;
		height: auto;
		margin-bottom: 20px;
	}
	.paywaytop{
		margin:0 50px 20px;

		dl{
			padding: 32px 0 20px;
			user-select: none;
			dd{
				padding: 24px 0 10px 0;
				overflow: hidden;
				span,i{
					float: left;
					line-height:34px;
					position: relative;
					height: 34px;
				}
				i{
					/*width: 184px;*/
					input{
						height: 100%;
						width: 100%;
						box-sizing: border-box;
						border: 2px solid #999;
						padding-left: 10px;
						font-size: 16px;
					}
					.inputActive{
						border: 2px solid #009e4d;
					}
				}


				i:after{
					content: '元';
					position: absolute;
					top: 0;
					right: -30px;
				}
				.recharge{
					margin-right: 10px;
				}

			}
			.alert{
				padding: 0;
				text-indent: 84px;
				font-size: 14px;
				color: #999;
			}
		}
	}
	.payment .paybtnarea{
		margin: 18px 20px 0 50px ;
		padding-bottom: 30px;
	}
	.payment .payway{
		height: 130px;
		display: flex;
		align-items: center;
	}
	.payment .payway label{
		height: 50px;
		margin-left: 10px;
		display: inline-block;
		border:1px solid #D9D9D9;
	}
	.payment .payway label:hover{
		border:1px solid #E64346;
	}
	.payment .payway .redlabel{
		border:1px solid #E64346;
	}
	.payment .payway li{
		margin-right: 50px; 
		display: flex;
		align-items: center;
	}
	.payment .pay-submit{
		font-size: 20px;
		width: 150px;
		height: 50px;
		line-height: 50px;
		color: #fff;
		background: #E64346;
	}
	.payment .time{
		color: #E64346;
	}
	.payment .checkbtnarea{
		/*padding: 20px;*/
		text-align: center;
	}
	
	.payment .checkbtnarea p{
		margin-top: 10px;
		font-size:20px;
		font-weight: 700;
		transform: scale(.9);
        -o-transform: scale(.9);
        -ms-transform: scale(.9);
        -moz-transform: scale(.9);
        -webkit-transform: scale(.9);
		color: #333;
	}
	.payment .checkbtnarea .checkbtnarea-p{
		font-weight: 100;
		font-size: 18px;
		color: #999;
		margin: 14px 0 40px;
		transform: scale(1);
        -o-transform: scale(1);
        -ms-transform: scale(1);
        -moz-transform: scale(1);
        -webkit-transform: scale(1);
	}
	.indentinfo {
		padding-top: 30px;
		overflow: hidden;
		h5{
			padding: 30px 0 12px;
		}
	}
	.indentinfo h4 {
		font-size: 16px;
		color: #333;
		font-weight: 900;
	}
	.totalprice {
		margin: 0 50px;
		font-size: 16px;
	}
	.totalmoney {
		font-size: 20px;
		color: #E64346;
	}
	.checkbtnarea a{
		display: inline-block;
	    text-decoration: none;
	    color: #333;
	    font-size: 18px;
	    width: 140px;
	    padding: 8px 0;
	    border-radius: 5px;
	    outline: none;
	    line-height: 18px;
	    text-align: center;
	    -o-box-sizing: border-box;
	    -ms-box-sizing: border-box;
	    box-sizing: border-box;
	    border: 2px solid #009e4d;
	    background: #009e4d;
	    color: #fff;
	}
	.checkbtnarea .btn-repay{
		background: #fff;
		border: 2px solid #f3f3f3;
		margin-left: 18px;
		color: #333;
	}
</style>



