<template>
	<div class="payment">

		<mineHeader></mineHeader>

		<div class="paymentcenter">

			<indentStatus>
		  		<span slot="indentName">支付订单</span>
		  	</indentStatus>

			<div class="payway-area">
				<div class="paywaytop">
					<div class="indentinfo">
						<h4>提交订单成功，请立即完成支付！</h4>
						<p class="paytotast">
							<i class="iconfont icon-tishi1"></i>
							请您在提交订单成功后24小时内完成支付，否则订单将被自动取消
						</p>
					</div>

					<div class="indentinfo indentinfo2"><h4>选择支付方式</h4></div>
					<ul class="payway">
						<li v-for="(item,index) in payWays" key="index">
							<input 
								type="radio" 
								:id="item.name"  
								name="payment" 
								v-on:click="choicePayway(item.name)" 
							/>
							<label 
								:for="item.name" 
								:class="[
									payWay==item.name?'redlabel':''
								]"
							>
								<img :src="item.img" />
							</label>
						</li>
					</ul>
				
					<transition name="fade">
						<div class="indentinfo" v-if="balanceSign">
							<h4>余额支付</h4>
							<div class="balance-wrap">
								<div class="balance-left">
									<span>账户余额：</span>
									<i>{{available_balance}}</i>
								</div>

								<!-- 余额不足 显示充值入口 -->
								<div class="balance-center" v-if="available_balance < paid_money">
									<span>余额不足，请</span>
									<a href="/recharge">点击充值</a>
								</div>
								<div class="balance-rigth">
									<span>支付：</span>
									<i>{{paid_money}}</i>
								</div>
							</div>
							<!-- 余额不足将隐藏发送验证码 -->
							<h5>验证手机号</h5>
							<div 
								class="code-wrap"
								:class="[
									codeInputSign ? 'inputActive' : ''
								]"
							>
								<span>
									<em>
										<input 
											type="tel" 
											v-model="codeNum0" 
											ref="codeNum0" 
											maxlength="1"
											@focus="codeNumFocus(0)"
											@keydown.8="deleteCode($event,0)"
										/>
									</em>
									<em>
										<input 
											type="tel" 
											v-model="codeNum1" 
											ref="codeNum1" 
											maxlength="1"
											@focus="codeNumFocus(1)"
											@keydown.8="deleteCode($event,1)"
										/>
									</em>
									<em>
										<input 
											type="tel" 
											v-model="codeNum2" 
											ref="codeNum2" 
											maxlength="1"
											@focus="codeNumFocus(2)"
											@keydown.8="deleteCode($event,2)"
										/>
									</em>
									<em>
										<input 
											type="tel" 
											v-model="codeNum3" 
											ref="codeNum3" 
											maxlength="1"
											@focus="codeNumFocus(3)"
											@keydown.8="deleteCode($event,3)"
										/>
									</em>
								</span>
								<i v-text="sendText" @click="sendCode"></i>
							</div>
						</div>
					</transition>

				</div>

				<div v-model="paid_money" class="totalprice">应付金额：<span class="totalmoney">￥{{paid_money}}</span></div>

				<div class="paybtnarea">
					<input 
						type="button" 
						class="pay-submit"
						value="立即支付" 
						@click="paySubmit"/>
				</div>

				<el-dialog 
					v-model="dialogVisible" 
					size="tiny" 
					:close-on-click-modal='false' 
					:close-on-press-escape='false' 
					:show-close="false"
				>
					<div class="img-wrap">
						<img src="data:image/png;base64," />
					</div>
					<div class="checkbtnarea">
						<p>请尽快支付完成，否则订单会被取消！</p>
						<p class="checkbtnarea-p">如有疑问，请联系客服0571-28270936</p>
						<router-link to="/mine/myIndent">
							支付完成
						</router-link>
						<a href="javascript:;" v-on:click="dialogVisible = false" class="btn-repay">重新支付</a>
					</div>
				</el-dialog>
			</div>
		</div>
		<index-footer></index-footer>
	</div>
</template>

<script>

	import mineHeader from '@/components/common/mine_header'
	import IndexFooter from '@/components/common/index_footer'
	import indentStatus from '@/components/common/indentStatus'

	export default {
		name: 'payment',
		data () {
			return {
				order_sn:'',
				paid_money:'',
				pay_remain_time:''*1,
				orderNumber:this.$route.query.trade_sn,
				order_id:this.$route.query.order_id || '',
				imgerweima:'',
				dialogVisible:false,
				trade_sn:'',

				mobile: this.$cookie.getCookie('8e3c824e1d6254b74a013799c1565538') || '',

				payWays:[
					{
						name:'alipay',
						img:'/static/images/payment-alipay.png'
					},
					{
						name:'weixin',
						img:'/static/images/payment-weixin.png'
					},
					{
						name:'allinpay',
						img:'/static/images/payment-allinpay.png'
					},
					{
						name:'balance',
						img:'/static/images/payment-balance.png'
					}
				],
				payWay:'',
				balanceSign:false,							// 余额展开下面的操作
				available_balance:'0.00',					// 余额

				codeNum0:'',								// 四位验证码
				codeNum1:'',
				codeNum2:'',
				codeNum3:'',

				sendText:'发送验证码',
				codeInputSign:false,						// 验证码输入框高亮标志
				wait:60,									// 验证码倒计时
			}
		},
		computed: {
            code() {
                return this.codeNum0 + this.codeNum1 + this.codeNum2 + this.codeNum3
            }
        },
		watch:{
			codeNum0(a,b){
				// 只允许输入数字
				if(!(a >=0)){
					this.codeNum0 = '';
					return false;
				}
				if(a != '' && this.codeNum1 ==''){
					this.$refs.codeNum1.focus();
				}
			},
			codeNum1(a,b){
				if(!(a >=0)){
					this.codeNum1 = '';
					return false;
				}
				if(a != '' && this.codeNum2 ==''){
					this.$refs.codeNum2.focus();
				}
			},
			codeNum2(a,b){
				if(!(a >=0)){
					this.codeNum2 = '';
					return false;
				}
				if(a != '' && this.codeNum3 ==''){
					this.$refs.codeNum3.focus();
				}
			},
			codeNum3(a,b){
				if(!(a >=0)){
					this.codeNum3 = '';
					return false;
				}
			}
		},
		components:{
			mineHeader,
			IndexFooter,
			indentStatus
		},
		beforeCreate(){
			let proof  = this.$cookie.getCookie('b61a9d162ac1dfaa22d0f05bdc7f6299');

	        if(proof == 1 ){

	        }else if(proof == -1){            // 审核中

	        	this.$anchu_message.warn('认证状态中，暂无权限访问该页面');
	        	this.$router.push('/');

	        }else if(proof == -2 || proof == -3 ){     // 审核没通过 未查询到数据

	        	this.$anchu_message.error('认证失败，请重新进行审核');
	            this.$router.push('/proof');

	        }else if(proof == -4 ){     // 未提交认证

	        	this.$anchu_message.warn('未提交认证，请先认证');
	            this.$router.push('/proof');

	        }else{
	        	this.$router.push('/login');
	        }


	        // jh电脑 该页面获取不到cookie 还未找到原因
	        // 如果有order页面标志 才能进入payment页面 否则进入 我的订单 页面 		    
	        // if(this.$cookie.getCookie('path2Sign')){
	        //     return;
	        // }else{
	        // 	this.$router.push('/mine/myIndent');
	        // }	

		},
		mounted(){
			const _this = this;

			this.getOrderMsg(this.orderNumber);
			// 监听鼠标按压事件
			document.onmouseup = function(e){
				if(document.activeElement.type == 'tel'){
					_this.codeInputSign = true;
				}else{
					_this.codeInputSign = false;
				}
			}
		},
		// 时间过滤器 天-时-分-秒
		filters:{
			timeStamp(second_time) {

				let time = parseInt(second_time) + "秒";

				if (parseInt(second_time) > 60) {
					let second = parseInt(second_time) % 60;
					let min = parseInt(second_time / 60);
					time = min + "分" + second + "秒";
					if (min > 60) {
						min = parseInt(second_time / 60) % 60;
						let hour = parseInt(parseInt(second_time / 60) / 60);
						time = hour + "小时" + min + "分" + second + "秒";
						if (hour > 24) {
							hour = parseInt(parseInt(second_time / 60) / 60) % 24;
							let day = parseInt(parseInt(parseInt(second_time / 60) / 60) / 24);
							time = day + "天" + hour + "小时" + min + "分" + second + "秒";
						}
					}
				}
				return time;
			},
		},
		methods:{
			codeNumFocus(index){
				if(this[`codeNum${index}`] != ''){
					return false;
				}
				if(this.codeNum0 == ''){213
					this.$refs.codeNum0.focus();
					return false;
				}
				if(this.codeNum1 == ''){
					this.$refs.codeNum1.focus();
					return false;
				}
				if(this.codeNum2 == ''){
					this.$refs.codeNum2.focus();
					return false;
				}
				if(this.codeNum3 == ''){
					this.$refs.codeNum3.focus();
					return false;
				}
			},
			// 获取账户余额信息
			getBalanceInfo(){
				const _this = this;
				$.ajax({
					type:"post",
					url:this.$api.recharge.info,
					dataType:"json",
					success:function(res){
						const data = res.data;

						// 余额 即可用余额
						_this.available_balance = data.available_balance;
					}
				});
			},

			// 获取验证码
	        sendCode(){

	            const _this = this;

	            $.ajax({
	                url:this.$api.register.send_validate_code,
	                data:{
	                    mobile:this.mobile,
	                    scene:2,
	                    type:'mobile'
	                },
	                dataType: "json",
	                type: 'post',
	                beforeSend:function () {
	                    if(_this.sendText != "发送验证码"){

	                        return false;
	                    }
	                },
	                success: function (res) {
	                    if (res.status == 1) {

	                       _this.$anchu_message.success(res.msg);
	                       _this.countDown();

	                    }else{
	                        _this.$anchu_message.error(res.msg);
	                    }
	                },
	                error:function (res) {
	                    _this.$anchu_message.error(res.msg);
	                }
	            })
	        },

	        //  验证码 余额 倒计时
	        countDown(){
	            const _this = this;

	            if (_this.wait <= 0) {

	                _this.sendText = "发送验证码";
	                _this.wait = 60;

	            } else {
	                _this.sendText = "请等待" + _this.wait + "s";
	                _this.wait--;

	                let timeOut = setTimeout(function () {

	                    _this.countDown();

	                    if(_this.wait <= 0){
	                        clearTimeout(timeOut); 
	                    }
	                },1000);
	            }
	        },

			// 删除验证码
			deleteCode(e,index){
				if(index == 0){
					return false;
				}
				// 删除后 让前一个聚焦
				if(this.$data['codeNum'+index] == ''){
					this.$refs['codeNum' + (index-1)].focus();
				}
			},

			//获取订单信息
			getOrderMsg(trade_sn){
				let _this = this;
				$.ajax({
					url: this.$api.order.detail,
					data:{
						trade_sn:trade_sn,
						id:_this.order_id
					},
					dataType: "json",
					type: 'get',
					success: function (res) {
						_this.order_sn = res.data.order_sn;
						_this.trade_sn = res.data.trade_sn;
						_this.order_id = res.data.order_id;
						_this.paid_money = res.data.total_amount;
						_this.time(res.data.pay_remain_time);
					},
					error:function (res) {
						_this.$anchu_message.error('网络繁忙');
					}
				});
			},

			// 倒计时更新
			time(o) {
				let _this = this;
				let second_time = parseInt(o);

				let SecondTime = setInterval(function(){
					
					// 计时结束 清除定时器
					if (second_time <= 0) {
						clearTimeout(SecondTime);
						_this.$anchu_message.error('订单超时请重新下单');
						_this.$router.push("/mine/orderDetail?id="+_this.orderNumber)
					}

					second_time--;
					_this.pay_remain_time = second_time;

					// 组件卸载后 清除定时器 防止在进入其他页面后 仍在执行
					if (_this && _this._isDestroyed){
						clearTimeout(SecondTime);
					}

				},1000);
			},

			// 选择支付方式
			choicePayway(e){
				this.payWay = e;
				if(e == 'weixin'){
					const _this = this;
					$.ajax({
						url: _this.$api.payment.wxqrcode,
						data:{
							trade_sn:_this.orderNumber
						},
						dataType: "json",
						type: 'get',
						success: function (res) {
							// console.log(res);
							_this.imgerweima = res.data
						},
						error:function (res) {
							_this.$anchu_message.error('网络繁忙');
						}
					})

				}else if(e == 'balance'){
					this.balanceSign = true;
					// 获取账户余额信息
					this.getBalanceInfo();
					return false;
				}

				this.balanceSign = false;
			},

			// 支付宝 银联支付
			postWindow(url,name1,name2,value1,value2){

				//创建表单对象
				let _form = $("<form></form>",{
					'id':'tempForm',
					'method':'post',
					'action':url,
					'target':'_blank',
					'style':'display:none'
				}).appendTo($('body'));

				//将隐藏域加入表单
				_form.append($("<input>",{'type':'hidden','name':name1,'value':value1}));
				_form.append($("<input>",{'type':'hidden','name':name2,'value':value2}));

				//触发提交事件
				_form.trigger('submit');

				//表单删除
				_form.remove();
			},

			// // 余额支付
			// postWindow(url,val1,val2,val3,val4){

			// 	//创建表单对象
			// 	let _form = $("<form></form>",{
			// 		'id':'tempForm',
			// 		'method':'post',
			// 		'action':url,
			// 		'target':'_blank',
			// 		'style':'display:none'
			// 	}).appendTo($('body'));

			// 	//将隐藏域加入表单
			// 	_form.append($("<input>",{'type':'hidden','name':'code','value':val1}));
			// 	_form.append($("<input>",{'type':'hidden','name':'mobile','value':val2}));
			// 	_form.append($("<input>",{'type':'hidden','name':'trade_sn','value':val3}));
			// 	_form.append($("<input>",{'type':'hidden','name':'pay_code','value':val4}));

			// 	//触发提交事件
			// 	_form.trigger('submit');

			// 	//表单删除
			// 	_form.remove();
			// },

		// 立即支付 按钮
		paySubmit(){

			if(this.payWay == ''){
				this.$anchu_message.warn('请选择支付方式');
				return false
			}

			const _this = this;
			$.ajax({
				url: this.$api.payment.checkUserBack,
				dataType: "json",
				type: 'get',
				async:false,
				success: function (res) {

					// 账号被拉黑
					if(res.error == 1){
						_this.$anchu_message.error(res.error_msg);
						return false;
					}else{

						if (_this.payWay == 'alipay') {

							// 进入paywaySuccess页面 标志 
			  				_this.$cookie.setCookie('path4Sign','true','h2');
							_this.postWindow(
								`${_this.$api.payLink}/index.php/Home/Payment/getCode`,
								'trade_sn',
								'pay_radio',
								_this.trade_sn,
								'pay_code=alipay',
							);

							_this.dialogVisible = true;

						}else if (_this.payWay == 'weixin'){

							// 进入paywayWeixin页面 标志 
			  				_this.$cookie.setCookie('path3Sign','true','s10');
							_this.$router.push({
						    	path:'/paywayWeixin',
						    	query:{
						    		'trade_sn': _this.trade_sn,
						    		'order_id':_this.order_id
						    	}
							});

							_this.dialogVisible = true;

							
						}else if(_this.payWay == 'allinpay'){

			                // 进入paywaySuccess页面 标志 
			  				_this.$cookie.setCookie('path4Sign','true','h2');

			                _this.postWindow(
			                	`${_this.$api.payLink}/api.php/Home/Payment/getCode.html`,
			                	'trade_sn',
			                	'pay_radio',
			                	_this.trade_sn,
			                	'pay_code=allinpay'
			                	);
			                _this.dialogVisible = true;
			        	}else if(_this.payWay == 'balance'){

			                if(_this.code.length != 4){
			                	_this.$anchu_message.warn('请输入正确的验证码');
			                	return false;
			                }

							$.ajax({
								url: _this.$api.payment.payment,
								data:{
									code:_this.code,
				                	mobile:_this.mobile,
				                	trade_sn:_this.trade_sn,
				                	pay_code:'wallet'
								},
								dataType: "json",
								type: 'post',
								success: function (res) {

									console.log(res)
									
									if(res.status == 0){
										_this.$anchu_message.success(res.message);
										_this.dialogVisible = true;
										_this.$router.push('/mine/myIndent');

									}else if(res.status == -1){
										_this.$anchu_message.error(res.msg);

									}else if(res.status == -2){
										_this.$anchu_message.error('余额不足，请及时充值，或选择其他方式支付');
									}
								},
								error:function (res) {
									_this.$anchu_message.error('网络繁忙');
								}
							})





			                
			        	}

					}
				},
				error:function (err) {
					_this.$anchu_message.error('网络繁忙');
				}
			});


        	
        	
        }
    }
}
</script>

<style>
	.payment .el-dialog__body{
		padding: 30px 0;
	}
 	.payment .el-dialog--tiny{
 		width:450px;
 		border-radius: 5px;
 		-moz-border-radius: 5px;
		-webkit-border-radius: 5px;
 	}
	.payment .el-dialog__header{
	  padding: 0;
	}

	.payment .img-wrap{
		text-align: center;
	}
	.img-wrap img{
		display: inline-block;
	}

</style>

<style lang="scss" scoped>

	.fade-enter-active {
	  transition: opacity .5s
	}
	.fade-leave-active{
		transition: opacity .2s
	}
	.fade-enter,.fade-leave-to{
	  opacity: 0;
	}
	.code-wrap{
		overflow: hidden;
		span{
			float: left;
			overflow: hidden;
			border: 1px solid #d8d8d8;
			border-right: 0;
			em{
				float: left;
				width: 36px;
				height: 36px;
				border-right: 1px solid #d8d8d8;
				input{
					display: inline-block;
					width: 100%;
					height: 100%;
					line-height: 36px;
					text-align: center;
					font-weight: 600;
					font-size: 16px;
				}
			}
		}
		i{
			float: left;
			line-height: 36px;
			margin-left: 12px;
			padding: 0 12px;
			background: #ebebeb;
			cursor: pointer;
			font-size: 14px;
		}

	}
	.inputActive{
		span{
			border: 1px solid #009e4d;
			border-right: 0;
			em{
				border-right: 1px solid #009e4d;
			}
		}
	}
	.balance-wrap{
		padding: 17px 15px;
		background: #fffcfb;
		border: 1px solid #e64346;
		margin-top: 15px;
		line-height: 24px;
		overflow: hidden;
	}
	.balance-left{
		color: #999;
		float: left;
		i{
			color: #333;
			&:after{
				content:'元';
			}
		}
	}
	.balance-center{
		float: left;
		margin-left: 15px;
		background: #f2f2f2;
		padding: 0 15px;
		border-radius: 14px;
		font-size: 14px;
		a{
			color: #009e4d;
			text-decoration: underline;

		}
	}
	.balance-rigth{
		float: right;
		color: #999;
		i{
			color: #e64346;
			&:after{
				color: #999;
				content:'元';
			}
		}
	}
	.payment{
		width: 100%;
		min-width: 1210px;
		background: #f5f5f5;
	}
	.payment .paymentcenter{
		width: 1200px;
		margin: 0 auto;
	}
	.payment .title{
		margin-bottom: 13px;
	}
	.payment .ordermsg{
		display: flex;
		justify-content: space-between;
		margin-bottom: 13px;
	}
	.payment .payway-area{
		background:#fff;
		width: 100%;
		height: auto;
		margin-bottom: 20px;
	}
	.payment .paywaytop{
		margin:0 50px 46px;
	}
	.payment .paybtnarea{
		margin: 18px 20px 0 50px ;
		padding-bottom: 30px;
	}
	.payment .payway{
		height: 130px;
		display: flex;
		align-items: center;
	}
	.payment .payway label{
		height: 50px;
		margin-left: 10px;
		display: inline-block;
		border:1px solid #D9D9D9;
	}
	.payment .payway label:hover{
		border:1px solid #E64346;
	}
	.payment .payway .redlabel{
		border:1px solid #E64346;
	}
	.payment .payway li{
		margin-right: 50px; 
		display: flex;
		align-items: center;
	}
	.payment .pay-submit{
		font-size: 20px;
		width: 150px;
		height: 50px;
		line-height: 50px;
		color: #fff;
		background: #E64346;
	}
	.payment .time{
		color: #E64346;
	}
	.payment .checkbtnarea{
		/*padding: 20px;*/
		text-align: center;
	}
	
	.payment .checkbtnarea p{
		margin-top: 10px;
		font-size:20px;
		font-weight: 700;
		transform: scale(.9);
        -o-transform: scale(.9);
        -ms-transform: scale(.9);
        -moz-transform: scale(.9);
        -webkit-transform: scale(.9);
		color: #333;
	}
	.payment .checkbtnarea .checkbtnarea-p{
		font-weight: 100;
		font-size: 18px;
		color: #999;
		margin: 14px 0 40px;
		transform: scale(1);
        -o-transform: scale(1);
        -ms-transform: scale(1);
        -moz-transform: scale(1);
        -webkit-transform: scale(1);
	}
	.indentinfo {
		padding-top: 30px;
		overflow: hidden;
		h5{
			padding: 30px 0 12px;
		}
	}
	.indentinfo2{
		border-top: 1px solid #ebebeb;
	}
	.indentinfo h4 {
		font-size: 16px;
		color: #333;
		font-weight: 900;
	}
	.indentinfo p {
		font-size: 14px;
		color: #999;
		margin: 20px 0 36px 0;
	}
	.indentinfo p i {
		font-size: 12px;
		background: #fff;
		color: #ffcd35;
	}
	.totalprice {
		margin: 0 50px;
		font-size: 16px;
	}
	.totalmoney {
		font-size: 20px;
		color: #E64346;
	}
	.checkbtnarea a{
		display: inline-block;
	    text-decoration: none;
	    color: #333;
	    font-size: 18px;
	    width: 140px;
	    padding: 8px 0;
	    border-radius: 5px;
	    outline: none;
	    line-height: 18px;
	    text-align: center;
	    -o-box-sizing: border-box;
	    -ms-box-sizing: border-box;
	    box-sizing: border-box;
	    border: 2px solid #009e4d;
	    background: #009e4d;
	    color: #fff;
	}
	.checkbtnarea .btn-repay{
		background: #fff;
		border: 2px solid #f3f3f3;
		margin-left: 18px;
		color: #333;
	}
</style>



